.translate-container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f7f7f7;
}

.translate-title {
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
    color: #333;
}

.translate-form {
    margin-bottom: 10px;
}

.translate-label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    color: #333;
}

.translate-textarea {
    width: 100%;
    padding: 10px;
    resize: none;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.translate-select {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.translate-button {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 4px;
}

.translated-text {
    margin-top: 20px;
    background-color: #f1f1f1;
    padding: 10px;
    border-radius: 4px;
}

.translated-text strong {
    font-weight: bold;
}